$component: --mt-paragrafo;

:host {
  #{$component}-text-color: var(--mt-color-textos-basico);
  #{$component}-text-font-family: 'Noto Sans', sans-serif;
  #{$component}-text-font-weight: 400;
  #{$component}-text-text-transform: none;
  #{$component}-text-font-size: 16px;

  #{$component}-strong-font-weight: 700;
  #{$component}-strong-text-transform: uppercase;
  #{$component}-strong-font-style: italic;

  display: block;
}

.mt-paragrafo {
  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;

    strong {
      font-weight: var(#{$component}-strong-font-weight);
      text-transform: var(#{$component}-strong-text-transform);
      font-style: var(#{$component}-strong-font-style);
    }

    u {
      color: var(--mt-color-textos-underline);
    }
  }
}
